<template>
    <div class="album py-5 bg-light">
        <div class="container">
            <!-- UserProfileCard.vue -->
            <UserProfileCard :initial-profile="Profile" :initial-is-followed="isFollowed"/>
            <div class="row">
                <div class="col-md-4">
                    <!-- UserFollowingsCard.vue -->
                    <UserFollowingsCard :initial-profile="Profile"/>
                    <br>
                    <!-- UserFollowersCard.vue -->
                    <UserFollowersCard :initial-profile="Profile"/>
                </div>
                <div class="col-md-8">
                    <!-- UserCommentsCard.vue -->
                    <UserCommentsCard :initial-profile="Profile"/>
                    <br>
                    <!-- UserFavoritedRestaurantsCard.vue -->
                    <UserFavoritedRestaurantsCard :initial-profile="Profile"/>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import UserProfileCard from '../components/UserProfileCard.vue'
import UserFollowingsCard from '../components/UserFollowingsCard.vue'
import UserFollowersCard from '../components/UserFollowersCard.vue'
import UserCommentsCard from '../components/UserCommentsCard.vue'
import UserFavoritedRestaurantsCard from '../components/UserFavoritedRestaurantsCard.vue'
import usersAPI from '../apis/users'
import { Toast } from '../utils/helpers'

export default {
    data() {
        return {
            Profile: {
                id: '',
                name: '',
                email: '',
                password: '',
                isAdmin: false,
                image: '',
                createdAt: '',
                updatedAt: '',
                Comments: [],
                FavoritedRestaurants: [],
                Followers: [],
                Followings: [],
            },
            isFollowed: false
        }
    },
    components: {
        UserProfileCard,
        UserFollowingsCard,
        UserFollowersCard,
        UserCommentsCard,
        UserFavoritedRestaurantsCard,
    },
    created() {
      const { id } = this.$route.params
      this.fetchUser(id)
    },
    methods: {
      async fetchUser(userId) {
        try {
          const { data } = await usersAPI.get({ userId })
          const {
            id,
            name,
            email,
            password,
            isAdmin,
            image,
            createdAt,
            updatedAt,
            Comments,
            FavoritedRestaurants,
            Followers,
            Followings
          } = data.profile
          const { isFollowed } = data

          this.Profile = {
            id,
            name,
            email,
            password,
            isAdmin,
            image,
            createdAt,
            updatedAt,
            Comments,
            FavoritedRestaurants,
            Followers,
            Followings
          }

          this.isFollowed = isFollowed
          
        } catch(err) {
          Toast.fire({
            icon: 'error',
            title: '無法取得使用者資料，請稍後再試'
          })
        }
        
      }
    },
    beforeRouteUpdate(to, from, next) {
      const { id } = to.params
      this.fetchUser(id)
      next()
    }
}
</script>