<template>
    <div class="card mb-3">
        <div class="row no-gutters">
            <div class="col-md-4">
                <img :src="profile.image" width="300px" height="300px">
            </div>
            <div class="col-md-8">
            <div class="card-body">
                <h5 class="card-title">{{profile.name}}</h5>
                <p class="card-text">
                {{profile.email}}
                </p>
                <ul class="list-unstyled list-inline">
                <li><strong>{{profile.Comments.length}}</strong> 已評論餐廳</li>
                <li><strong>{{profile.FavoritedRestaurants.length}}</strong> 收藏的餐廳</li>
                <li><strong>{{profile.Followings.length}}</strong> followings (追蹤者)</li>
                <li><strong>{{profile.Followers.length}}</strong> followers (追隨者)</li>
                </ul>
                <p>
                    </p>
                <div v-if="!isCurrentUserFit">
                    <button type="submit" class="btn btn-danger" v-if="isFollowed" @click.stop.prevent="deleteFollowing">取消追蹤</button>
                    <button type="submit" class="btn btn-primary" v-else @click.stop.prevent="addFollowing">追蹤</button>
                </div>
                <router-link :to="{name: 'user-edit', params: profile.id}" v-if="isCurrentUserFit">
                    <button class="btn btn-primary">修改資料</button>
                </router-link>
                
                <p></p>
            </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
import usersAPI from '../apis/users'
import { Toast } from '../utils/helpers'
export default {
    props: {
        initialProfile: {
            type: Object,
            required: true
        },
        initialIsFollowed: {
            type: Boolean,
            required: true
        }
    },
    data() {
        return {
            profile: this.initialProfile,
            isFollowed: this.initialIsFollowed,
            isCurrentUserFit: false
        }
    },
    methods: {
        async addFollowing() {
            try {
                const { data } = await usersAPI.addFollowing({ userId: this.profile.id })
                if(data.status !== 'success') {
                    throw new Error(data.message)
                }
                Toast.fire({
                    icon: 'success',
                    title: '加入追蹤成功'
                })
                this.isFollowed = true
            } catch(err) {
                Toast.fire({
                    icon: 'error',
                    title: '無法加入追蹤，請稍後再試'
                })
            }
        },
        async deleteFollowing() {
            try {
                const { data } = await usersAPI.deleteFollowing({ userId: this.profile.id })
                if(data.status !== 'success') {
                    throw new Error(data.message)
                }
                Toast.fire({
                    icon: 'success',
                    title: '取消追蹤成功'
                })
                this.isFollowed = false
            } catch(err) {
                Toast.fire({
                    icon: 'error',
                    title: '無法取消追蹤，請稍後再試'
                })
            }
        }
    },
    watch: {
        initialProfile(newValue) {
            this.profile = {
                ...this.profile,
                ...newValue
            }
            this.isCurrentUserFit = (this.profile.id === this.currentUser.id) ? true : false
        },
        initialIsFollowed(newValue) {
            this.isFollowed = newValue
        }
    },
    computed: {
        ...mapState(['currentUser'])
    }
}
</script>